.project-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  @media screen and (max-width: 1600px) {
    height: 100%;
    margin-top: 350px;
  }
  @media screen and (max-width: 768px) {
    margin-top: 0;
  }

  .redirect {
    transition: 0.5s;
    &:hover {
      transform: scale(1.02);
      transition: 0.5s;
    }
  }

  .project-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px;
    max-width: 1400px;
    margin: 50px auto 300px;
    @media screen and (min-width: 3200px) {
      margin: 300px auto 300px;
  }
    @media screen and (max-width: 1600px) {
      flex-direction: column;
      align-items: center;
      margin: 150px auto 50px;
    }

    .project-card {
      background-color: rgba($white, 0.7);
      border-radius: 10px;
      padding: 60px;
      text-align: center;
      color: $black;
      width: 100%;
      height: auto;
      transition: 0.5s;
      @media screen and (max-width: 768px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: rgba($white, 0.6);
        padding: 2px;
      }



      h2 {
        font-size: 36px;
        margin-bottom: 30px;
      }

      p {
        font-size: 24px;
        margin-bottom: 30px;
      }

      img {
        width: 100%;
        height: 300px;
        object-fit: cover;
        border-radius: 10px;
      }

      .button {
        display: inline-block;
        padding: 15px 30px;
        font-size: 24px;
        background-color: #546fe4;
        color: #fff;
        text-decoration: none;
        border-radius: 10px;
        transition: background-color 0.3s ease;

        &:hover {
          background-color: #4054b2;
        }
      }

      @media screen and (max-width: 1600px) {
        width: 100%;
        max-width: 600px;
      }
    }
  }
}
