main {
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    margin: 250px auto 50px;
    overflow: hidden;
    span {
        font-size: 5rem;
    }
    @media screen and (max-width: 3200px) {
        margin: 100px auto 50px;
    }
    @media screen and (max-width: 1600px) {
        margin: 500px auto 50px;
    }
    @media screen and (max-width: 768px) {
        margin: 125px auto 50px;
        .letter, .letter-bis {
            font-size: 3rem;
        }
    }
}
h2 {
    font-size: 2.5rem;
    @media screen and (max-width: 768px) {
        font-size: 1.5rem;
    }
}
.blob-photo-container {
    position: relative;
    display: flex;
    height: 30vh;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 768px) {
        height: 30vh;
    }
    .blob-photo {
        user-select: none;
        position:absolute;
        display: block;
        border-radius: 50%;
        box-shadow: 0 0 20px $color-2;
    }
    .blob-photo img {
        border-radius: 50%;
        object-fit:cover;
        height: 100%;
        width: 100%;
    }
}

.skills-container {
    border: 2px solid white;
    border-radius: 50px;
    background-color: rgba($black, 0.95);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 40%;
    padding: 20px 60px 60px;
    margin-bottom: 50px;
    @media screen and (max-width: 1600px) {
        width: 60%;
    }
    @media screen and (max-width: 768px) {
        width: 90%;
      }
    .front-container, .back-container, .other-container { 
        display: flex;
        flex-wrap: wrap;
        width: 80%;
        justify-content: start;
        gap: 5%;
        align-items: center;
        @media screen and (max-width: 768px) {
            justify-content: center;
            width: 100%;
        }
        .blob-skills {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            background-color: $black;
            box-shadow: 0 0 20px rgba(157, 39, 3, 0.55);
            height: 80px;
            width: 80px;
            color: $black;
            position: relative;
            margin: 30px 0;
            img {
              border-radius: 50%;
              object-fit:contain;
              height: 90px;
              width: 90px;
            }
            p {
                width: 150px;
                text-align: center;
            }
        }
        .skills-label {
            color: $white;
            position: absolute;
            bottom: -40px;
            font-size: 18px;
        }
    }
    .line {
        border-top: 1px solid $color-2;
        margin: 30px auto 10px;
        width: 80%;
    }
}